.buttonArea{
  width: 600px;
  margin-left: 180px;
}

.calendarArea > .container {
  width: 600px;
  height: 600px;
  margin: 0 auto;
  padding-top: 30px;
}


/*    */
.scheduleTable  {
  width: 600px;
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0 auto;
  margin-top: 50px;
}

/*    */
.scheduleTable  th {
  background-color: #e2e6ff;
  font-weight: bold;
  padding: 8px;
  border: 1px solid #dddddd;
  text-align: center;
}


.scheduleTable tr {
  background-color: #fff;
}

/*    */
.scheduleTable td {
  padding: 8px;
  border: 1px solid #dddddd;
}

/*       */
.scheduleTable td {
  text-align: center;
}

/*     */
.scheduleTable td:first-child {
  font-weight: bold;
}

.container::before {
  background-image:none;
  box-shadow: none;
}